import React, {Component} from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import _ from "lodash";

import OraMobileOffersDocumentsSection from "../../configurator/components/OraMobileOffersDocumentsSection";
import {OraOffersDocumentsExpander} from "../../magnum2/components/documents/OraDocumentsSection";
import {getStaticSimFreeDocuments} from "../../simfree/actions/app";
import {fetchDocuments} from "../../magnum2/actions/data";
import OfferType from "../../simfree/constants/OfferTypeEnum";

class OraOffersDocumentsContainer extends Component {
    constructor(props) {
        super(props);
        if (this.props.isFut === 'false') {
           this.props.getStaticDocuments();
        }
    }

    componentDidMount() {
        if (this.props.isFut === 'false') {
            this.props.getStaticDocuments();
        }
    }

    render() {
        let regulationsList;
        let agreementsList;
        let pricesList;
        let depositList;

        if (this.props.selectedOfferType === OfferType.CONVERGENT) {
            return (
                <OraOffersDocumentsExpander
                    descriptions={{
                        sectionName: 'Regulaminy, Cenniki, Umowy',
                        noDocuments: 'Brak dokumentów',
                        fetchDocumentsMessage: 'Trwa wczytywanie dokumentów',
                        downloadDocumentMessage: 'Trwa pobieranie dokumentu',
                    }}
                    documents={this.props.magnumDocuments.list}
                    documentsMetadata={this.props.magnumDocuments.metadata}
                    durationList={this.props.magnumDurationList}
                    fetchDocuments={this.props.fetchDocuments}
                    isLoading={this.props.magnumDocuments.loading}
                />
            );
        }

        if (!_.isEmpty(this.props.documents)) {
            this.props.documents.sort(function (a,b) {
                if (a.priority === b.priority) {
                    return a.documentDescription.localeCompare(b.documentDescription);
                } else {
                    return a.priority - b.priority;
                }
            });
            agreementsList = this.props.documents.filter(element => this.props.filters.agreementsList.indexOf(element.documentType) > -1);
            pricesList = this.props.documents.filter(element => this.props.filters.pricesList.indexOf(element.documentType) > -1);
            regulationsList = this.props.documents.filter(element => this.props.filters.regulationsList.indexOf(element.documentType) > -1);
            depositList = this.props.documents.filter(element => this.props.filters.depositList.indexOf(element.documentType) > -1);
        }

        return (!_.isEmpty(this.props.documents) && !_.isEmpty(regulationsList.concat(agreementsList, pricesList, depositList))) ?
            (
                <OraMobileOffersDocumentsSection
                    regulationsList={regulationsList}
                    agreementsList={agreementsList}
                    pricesList={pricesList}
                    depositList={depositList}
                    selectedOfferType={this.props.selectedOfferType}
                />
            ) : <div></div>;
    }

}

OraOffersDocumentsContainer.propTypes = {
    isFut: PropTypes.string,
    documents: PropTypes.arrayOf(PropTypes.shape({
        documentType: PropTypes.string
    })),
    magnumDocuments: PropTypes.object,
    filters: PropTypes.object,
    magnumDurationList: PropTypes.object,
    loaderOptions: PropTypes.string,
    selectedOfferType: PropTypes.string,
    getStaticDocuments: PropTypes.func,
    fetchDocuments: PropTypes.func
};

const mapStateToProps = state => ({
    magnumDocuments: state.magnum.documents,
    magnumDurationList: state.magnum.durationList
});

const mapDispatchToProps = dispatch => ({
    getStaticDocuments: () => dispatch(getStaticSimFreeDocuments()),
    fetchDocuments
});

export default connect(mapStateToProps, mapDispatchToProps)(OraOffersDocumentsContainer);